import colorCatalog from '../data/colors.json';

export const getColorName = (color) => {
  if (color && color.indexOf('-') !== -1) {
    return `(${color.split('-')[0]}-${color.split('-')[1]}) ${
      colorCatalog[color.split('-')[0]]
    } / ${colorCatalog[color.split('-')[1]]}`;
  }
  return color && `(${color}) ${colorCatalog[color]}`;
};

export const imageFallback = (ev) => {
  console.log('here', ev);
  ev.target.src =
    // 'https://www.velillaconfeccion.com/media/catalog/product/cache/1/image/280x440/9df78eab33525d08d6e5fb8d27136e95/placeholder/default/noimage.png';
    '/imagen-no-disponible.png';
};

export const getProductURL = (type, product) => {
  return type === 'list'
    ? `https://www.velillaconfeccion.com/media/catalog/product/cache/1/small_image/150x185/9df78eab33525d08d6e5fb8d27136e95/${
        product?.var
      }${product?.ref.replaceAll('-', '')}/default.jpg`
    : `https://www.velillaconfeccion.com/media/catalog/product/cache/1/image/280x440/9df78eab33525d08d6e5fb8d27136e95/${
        product?.var
      }${product?.ref.replaceAll('-', '')}`;
};
